interface ServiceContentProps {
  content: {
    title: string
    sections: Array<{
      heading: string
      content: string
    }>
  }
}

export default function ServiceContent({ content }: ServiceContentProps) {
  return (
    <main className="flex-1 p-8 md:p-12">
      <div className="max-w-3xl">
        <h2 className="text-3xl md:text-4xl font-bold text-slate-900 mb-8">{content.title}</h2>

        <div className="space-y-8">
          {content.sections.map((section, idx) => (
            <div key={idx}>
              <h3 className="text-xl font-bold text-slate-900 mb-3">{section.heading}</h3>
              <p className="text-gray-600 leading-relaxed">{section.content}</p>
            </div>
          ))}
        </div>
      </div>
    </main>
  )
}
